import * as React from 'react';
import * as styles from './FilmsFeed.module.scss';
import { FilmsCard } from './FilmsCard';
import { Carousel } from 'react-responsive-carousel/lib/js';
export const FilmsFeed = ({ films , isHome  })=>{
    let list = [];
    let page = 0;
    films.nodes.map((item, i)=>{
        if (i !== 0 && i % 3 == 0) {
            page++;
        }
        if (list[page] == null) {
            list[page] = [];
        }
        list[page].push(item);
    });
    return React.createElement("div", {
        className: "inner"
    }, React.createElement("div", {
        className: styles.posts
    }, React.createElement("div", {
        className: styles.postFeed
    }, React.createElement(Carousel, {
        showArrows: true,
        autoPlay: true,
        swipeable: true,
        emulateTouch: true,
        showStatus: false,
        showThumbs: false,
        infiniteLoop: true
    }, list.map((item, index)=>{
        return React.createElement("div", {
            className: styles.filmGroup,
            key: index
        }, item.map((post, i)=>{
            return React.createElement(FilmsCard, {
                post: post,
                i: i,
                key: i,
                isHome: isHome
            });
        }));
    })))));
};
